import React,{Component} from 'react';


class SignIn extends Component {
  constructor(props){
    super(props);
    this.state = {
      singInEmail: '',
      signInPassword:''
    }
  }
  
  onEmailChange = (event)=>{
    this.setState({singInEmail: event.target.value});
  }

  onPasswordChange = (event)=>{
    this.setState({signInPassword: event.target.value});
  }

  onSignInSubmit = ()=> {
    console.log('sdsd');
    fetch('https://immense-cove-34629.herokuapp.com/signin', {
      method: 'post',
      headers: {'Content-Type': 'application/json'},
      body : JSON.stringify({
        email: this.state.singInEmail,
        password: this.state.signInPassword
      })
    }).then(response => response.json()).then(data => {
      if(data.id){
         this.props.loadUser(data);
         this.props.onRouteChange('home');
      }
    })
    
  } 


  render(){
    const {onRouteChange} = this.props;
    return (
        <article className="br3 ba b--black-10 mv4 w-100 w-50-m w-25-l mw6 center shadow-5">
        <main className="pa4 black-80">
        <div className="measure">
          <fieldset id="sign_up" className="ba b--transparent ph0 mh0">
            <legend className="f1 fw6 ph0 mh0 center">Sign In</legend>
            <div className="mt3">
              <label className="db fw6 lh-copy f6" >Email</label>
              <input onChange={this.onEmailChange} className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="email"  name="email" id="email-address"/>
            </div>
            <div className="mv3">
              <label className="db fw6 lh-copy f6" >Password</label>
              <input onChange={this.onPasswordChange} className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="password" name="pw"  id="password"/>
            </div>
          </fieldset>
          <div className="">
            <input onClick={this.onSignInSubmit} className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib" type="submit" value="Sign in"/>
          </div>
          <div className="lh-copy mt3">
            <p onClick={() => onRouteChange('register')} href="#0" className="f6 link dim black db pointer">Register</p>
          </div>
        </div>
      </main>
      </article>
    );
}
}

export default SignIn;